                    "last_skill",
                    "npclevel",
                    "nearby_enemy",
                    "tbufftime",
                ],
                value: [
                    "buff_level",
                    "tbuff_level",
                    "bufftime",
                    "life",
                    "mana",
                    "rage",
                    "qidian",
                    "energy",
                    "sun",
                    "moon",
                    "skill_energy",
                    "last_skill",
                    "npclevel",
                    "nearby_enemy",
                    "tbufftime",
                ],
                relationRestricted: ["skill", "noskill", "last_skill"],
            },
            conditions: [
                {
                    name: "",
                    relation: "=",
                    value: "",
                    logic: "&",
                    subname: "",
                },
            ],
            conditionValueName: {
                buff: "存在增减益效果",
                buff_level: "增减益效果层数",
                nobuff: "不存在某增减益效果",
                bufftime: "增减益效果持续时间",
                life: "生命值和最大血量的比值",
                mana: "内力值和最大内力值的比值",
                rage: "剑气/尘身刀气/战意/怒气值",
                qidian: "纯阳气点",
                energy: "神机/竹雾刀气/格挡值",
                sun: "日灵/金屏刀气值",
                moon: "月魂值",
                sun_power: "满日状态",
                moon_power: "满月状态",
                skill_energy: "充能技能的当前充能层数",
                skill: "存在某技能/奇穴ID",
                noskill: "不存在某技能/奇穴ID",
                last_skill: "该宏最后一次释放的技能",
                npclevel: "目标NPC强度等级",
                nearby_enemy: "自身周围3尺以内敌人数量",
                skill_notin_cd: "技能调息完成",
                tbuff: "目标有增减益效果",
                tbuff_level: "目标有增减益效果层数",
                tnobuff: "目标不存在某增减益效果",
                tbufftime: "目标增减益效果持续时间",
            },
            logicDialogVisible: false,
            logicDialog: "",
            region: "",
            cmOptions: {
                tabSize: 4,
                lineNumbers: true,
                line: true,
                styleActiveLine: true,
                lineNumbers: true,
                mode: "text/x-macro",
                extraKeys: { Tab: "autocomplete" },
                matchBrackets: true,
                hintOptions: {
                    completeSingle: false,
                },
                // more CodeMirror options...
            },
            code: "",
        };
    },
    computed: {
        codemirror() {
            return this.$refs.cmEditor.codemirror;
        },
    },
    methods: {
        clickPlusCondition() {
            this.conditions.push({
                name: "",
                relation: "=",
                value: "",
                logic: "&",
                subname: "",
            });
        },
        clickMinusCondition(index) {
            this.conditions.splice(index, 1);
        },
        onConditionChange(index) {
            let thisName = this.conditions[index].name;
            let thisRelation = this.conditions[index].relation;
            if (
                thisName === "skill" ||
                thisName === "noskill" ||
                thisName === "last_skill"
            ) {
                if (thisRelation !== "=" && thisRelation !== "≠") {
                    // // 修改判断符号为等号
                    // let tmpArr = this.conditions
                    // tmpArr[index].relation = '='
                    // this.conditions = tmpArr
                    this.conditions[index].relation = "=";
                }
            }
        },
        insertLine() {
            let line = "/" + this.castType + " ";
            if (this.isConditional) {
                let allConditions = "[";
                for (let i = 0; i < this.conditions.length; ++i) {
                    let condition = this.conditions[i];
                    if (i !== 0) {
                        allConditions += condition.logic;
                    }
                    allConditions += condition.name;
                    if (
                        this.needsConditionParams.subname.includes(
                            condition.name
                        )
                    ) {
                        allConditions = allConditions + ":" + condition.subname;
                    }
                    if (
                        this.needsConditionParams.relation.includes(
                            condition.name
                        )
                    ) {
                        allConditions = allConditions + condition.relation;
                    }
                    if (
                        this.needsConditionParams.value.includes(condition.name)
                    ) {
                        allConditions = allConditions + condition.value;
                    }
                }
                allConditions += "]";
                line = line + allConditions + " ";
            }
            line = line + this.skill + "\n";
            this.code += line.replace(/buff_level/g, "buff");
        },
        onCmCodeChange(value) {
            // this.codemirror.showHint();
        },
        showRealLogic() {
            if (this.conditions <= 2) {
                return false;
            } else {
                let allConditions = "";
                for (let i = 0; i < this.conditions.length; ++i) {
                    let condition = this.conditions[i];
                    if (i === this.conditions.length - 1) {
                        allConditions +=
                            // condition.logic === "&" ? " 并且 " : " 或 ";
                            condition.logic === "&" ? " & " : " | ";
                    } else if (i !== 0) {
                        allConditions +=
                            // condition.logic === "&" ? " 并且（" : " 或（";
                            condition.logic === "&" ? " &（" : " |（";
                    }
                    // allConditions += this.conditionValueName[condition.name];
                    allConditions += condition.name;
                    if (
                        this.needsConditionParams.subname.includes(
                            condition.name
                        )
                    ) {
                        allConditions = allConditions + ":" + condition.subname;
                    }
                    if (
                        this.needsConditionParams.relation.includes(
                            condition.name
                        )
                    ) {
                        allConditions = allConditions + condition.relation;
                    }
                    if (
                        this.needsConditionParams.value.includes(condition.name)
                    ) {
                        allConditions = allConditions + condition.value;
                    }
                }
                for (
                    let parenthesis = 0;
                    parenthesis < this.conditions.length - 2;
                    ++parenthesis
                ) {
                    allConditions += "）";
                }
                this.logicDialog = allConditions.replace(/buff_level/g, "buff");
                this.logicDialogVisible = true;
            }
        },
        getUserId() {
            if (User.isLogin()) {
                this.uid = User.getInfo().uid;
            }
        },
    },
    filters: {},
    mounted: function() {
        // this.getUserId();
        // this.prepareMounted();
    },
    components: {
        codemirror,
        Nav,
        // Extend
    },
};
</script>

